footer{
    background-color: var(--cl-sixth);
    margin-top: 0.1rem;
    text-align: center;
    font-size: 0.9rem;
    padding: 3rem 0 0.5rem;
    position: relative;
    bottom: 0;
    left: 0;
    right: 0;
}
.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    display: inline-block;
    margin-bottom: 2rem;
}
.weblinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto 2rem;
    gap: 2rem;
}
.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}
.footer__socials a{
    background: var(--cl-primary);
    color: white;
    display: flex;
    justify-content: center;
    padding: 0.8rem;
    border-radius: 0.7rem;
    border: 1px solid transparent;
}
.footer__socials a:hover{
    background: transparent;
    color: var(--cl-primary);
    border-color: var(--cl-primary);
}
.footer__copyrights{
    color: var(--cl-primary);
    margin-bottom: 2rem;
}