.spost{
    display: grid;
    grid-template-columns: 0.7fr 1.3fr;
    gap: 20px;
    margin: 1rem auto;
}
.ptext h2{
    margin: 0;
    padding: 0;
}
.spost .image{
    max-width: 100%;
}
.ptext{
    display: grid;
    grid-template-rows: 0.5fr 1.5fr;
    gap: 10px;
}
.ptime{
    margin: 0 8px 0;
}