*{
    text-decoration: none;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
* p{
    cursor: default;
}
* a{
    cursor: pointer;
}
.head{
    color: #851e3e;
}
.a{
    color: #041730;
}
img{
    max-width: 100%;
}
body{
    background-color: var(--cl-sixth);
}
:root{
    --cl-primary: #041730;
    --cl-sec: #251e3e;
    --cl-third: #451e3e;
    --cl-fourth: #651e3e;
    --cl-fifth: #851e3e;
    --cl-sixth: rgb(51,51,51);
    --cl-seventh: #36454f;
}
.text{
    color: rgb(202, 198, 191);
}
.text2{
    color: #36454f;
}
.text3{
    color: var(--cl-fifth);
}
.btn{
    width: fit-content;
    text-align: center;
    height: fit-content;
    color: #041730;
    background-color: var(--cl-seventh);
    transition: 500ms ease;
    border-radius:1rem ;
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
}
.btn:hover{
    border-color:var(--cl-seventh);
    background-color: transparent;
}
section{
    min-height: 100vh;
}
