.title{
    text-align: center;
    color: cornsilk;
}
.pptext{
    text-align: center;
    color: #111;
}
.pptext time{
    margin-left: 0.2rem;
}
.editbtn{
  width: 8rem;
  margin-bottom: 1rem;
  display: inline-block;
  text-align: center;
}
.editdiv{
    display: flex;
    justify-content: center;
    margin-top: 1.2rem;
}
.PostPage_container{
    display: grid;
    grid-template-columns: 1fr;
    max-width: 80%;
}
.PostPage{
    display: flex;
    justify-content: center;
    flex-direction: row;
    max-width: 100%;
    text-align: center;
}
.ppheader{
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 1rem;
}
.ppimg{
    margin-top: 1rem;
    width: 100%;
    display: flex;
    justify-content: center;
    max-height: 500px;
}
.ppimg img{
    width: 70%;
    height: auto;
}
.ppcontent img{
    width: 40%;
    height: auto;
}
.ppcontent{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 1rem;
}
.ppcontent a{
    color: wheat;
}