@-webkit-keyframes fadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}
@keyframes fadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}
.Navbtn{
    display: block;
    height: 1.4rem;
    width: 2.4rem;
    color: var(--cl-fifth);
}
.navbtn{
    margin-top: auto;
    margin-bottom: auto;
}
.navbar__container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: var(--cl-sixth);
    padding: 0;
    height: 4rem;
    border-bottom: 1px solid transparent;
}
.left_nav,.right_nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-top: auto;
    margin-bottom: auto;
}
.left_nav{
    gap: 1.2rem;
    margin-left:0.8rem;
}
.left_nav h3{
    margin-top: 0.4rem;
}
.SideNav{
    display: none;
}
.SideNav:has(div.sideNav__container){
    display: block;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
    background: var(--cl-sixth);
    border: 1px solid transparent;
    text-align: center;
}
.sideNav__container{
    display:grid;
    grid-template-columns: 1fr;
    gap: 10px;
}
.other__container:has(div.sideNav__container){
    display: grid;
    grid-template-columns: 15% 85%;
    gap: 0;
}
.username{
    color: rgb(202, 198, 191);
    margin: 1rem auto 2rem;
}
/* --------------------------------Small Devices --------------------------------*/
@media screen and (max-width:600px){
.other__container:has(div.sideNav__container){
    grid-template-columns: 30% 70%;
}

}