.logup{
    height: 70vh;
    width: 80vw;
    margin: 1rem auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}
.logup input{
    color: #111 !important;
    background-color: #333 !important;
    margin: 0.5rem;
    padding: 1rem;
    box-sizing: border-box;
    font-size: large;
}
.logup input:focus{
    color: #111 !important;
    background-color: #333 !important;
}
.logup button{
    color: #111;
    background-color: #333;
    margin: 0.5rem;
    padding: 1rem;
}
.logup input::placeholder{
    color: #111;
    background-color: #333;
}
input:-webkit-autofill {
    -webkit-box-shadow:0 0 0 50px #333 inset; /* Change the color to your own background color */
    -webkit-text-fill-color: #111;
}
input:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0 50px #333 inset;
    -webkit-text-fill-color: #111;
}
.logup h2{
    font-size: 3rem;
    margin-bottom: 6rem;
}
.logup h5{
    font-size: 2rem;
    font-weight: bold;
}